* {
    margin: 0;
    box-sizing: border-box;
}
header {
    text-align: center;
    font-size: large;
    font-weight: bold;
    background-color: rgb(0, 0, 0);
    padding: 10px;
    color: white;
}
button.square {
    width: 150px;
    height: 150px;
    cursor: pointer; 
    outline: none;
    border: none;
    font-size: 5rem;
    background-color: rgb(226, 216, 216);
    margin-top: -1px;
    line-height: 100px;
}
button.square:nth-child(2n) {
    background-color: rgb(243, 235, 235);
}
button.square:hover {
    background-color: rgba(219, 76, 76, 0.788);
}
div.game {
    padding: 20px;
    display: flex;
    justify-content: center;
}
div.info {
    margin-left: 20px;
    padding: 20px;
    width: 300px;
}
div.board-row {
    display: grid;
    grid-template-columns: auto auto auto;
    justify-content: center;
}
p.player {
    font-size: large;
}

button.player-one {
    background-color: rgb(240, 240, 49);
}
button.playerlog {
    border: 1px solid black;
    margin-right: 1px;
    margin-top: 1px;
    cursor: pointer;
    outline: none;
    border-radius: 3px;
}
button.playerlog:hover {
    background-color: rgba(219, 76, 76, 0.788);
}
@media only screen and (max-width: 768px) {
    div.game {
        display: flex;
        flex-direction: column;
        width: 100%;
    }
    div.info {
        margin: auto;
        width: 300px;
    }
    button.square {
        width: 100px;
        height: 100px;
    }
}

